button, input, label, select {
  font-size: 1em;
}

.sr-only {
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  white-space: nowrap;
  border: none;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.select-a11y {
  position: relative;
}

.select-a11y.is-open .select-a11y__overlay {
  display: block;
}

.select-a11y input::-webkit-contacts-auto-fill-button {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
  right: 0;
  display: none !important;
}

.select-a11y-button {
  width: 100%;
  text-align: left;
  background-color: #dfdfe4;
  position: relative;
}

.select-a11y-button[aria-expanded="true"] {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.select-a11y-button[aria-expanded="true"] .select-a11y-button__icon {
  transform: rotate(180deg);
}

.select-a11y-button__clear {
  appearance: none;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  line-height: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 2rem;
}

.select-a11y-button__clear:before {
  content: "✕";
}

.select-a11y-button.select-a11y-button--no-selected-option + .select-a11y-button__clear {
  display: none;
}

.select-a11y-button__text {
  padding-right: 2.5rem;
}

.select-a11y-button__icon {
  line-height: 0;
  position: absolute;
  top: 50%;
  right: .5em;
}

.select-a11y-button__icon:before {
  content: "▼";
}

.select-a11y__selected-item {
  border: 1px solid #c3c4cc;
  padding: .2em .1em .2em .5em;
}

.select-a11y-delete__icon:before {
  content: "✕";
  color: red;
  line-height: 1;
}

.select-a11y-delete {
  background-color: #0000;
  border: 1px solid #0000;
}

.select-a11y-delete:focus {
  color: red;
  outline: 1px solid red;
}

.select-a11y-delete:hover {
  color: red;
  border-color: red;
}

.select-a11y__hidden {
  display: none;
}

.select-a11y__overlay {
  z-index: 10;
  background: #ecedef;
  border: 1px solid #dfdfe4;
  padding: .5em;
  position: absolute;
  left: 0;
  right: 0;
}

.select-a11y-suggestions {
  max-height: 10em;
  background-color: #fff;
  border: 1px solid #595b6a;
  margin-top: .5em;
  overflow-y: auto;
}

.select-a11y-suggestion {
  color: #595b6a;
  cursor: pointer;
  text-align: left;
  align-items: center;
  padding: .25rem .5rem .25rem 1.5rem;
  display: flex;
}

.select-a11y-suggestion:focus, .select-a11y-suggestion:hover {
  color: #363740;
  background-color: #dfdfe4;
}

.select-a11y-suggestion:focus {
  outline-offset: -1px;
  outline: 1px dotted #363740 !important;
}

.select-a11y-suggestion[aria-selected="true"] {
  color: #363740;
  background-color: #c3c4cc;
  position: relative;
}

.select-a11y-suggestion[aria-selected="true"]:before {
  content: "▸";
  position: absolute;
  top: 50%;
  left: .5em;
  transform: translateY(-50%);
}

.select-a11y-suggestion__image {
  width: 3rem;
  height: 3rem;
  object-fit: cover;
  object-position: center;
  margin-right: .5rem;
}

.select-a11y__no-suggestion {
  color: #595b6a;
  text-align: left;
  margin: 0;
  padding: 0 .5em;
}

.select-a11y__selected-list {
  margin: 0;
  padding: .5em 0;
}

.select-a11y__selected-list li {
  margin: 0 .3em .3em 0;
}

.select-a11y__selected-list li:before {
  content: "";
}

.select-a11y__selected-list li span {
  display: inline-block;
}

